@charset "UTF-8";/* GENERAL CSS */* {
  padding: 0;
  margin: 0;
}

a:focus {
  outline: #1d0029 solid 2px;
}

body {
  font-family: "Open Sans", sans-serif;
}/* Import de los partials*/.container {
  margin: 0 0.9375rem;
}/* CONTAINER @MEDIA *//* HEADER CSS*/.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 2;
}

.header-1 {
  background-color: rgba(9, 157, 141, 0.95);
  padding: 15px 0;
  text-align: center;
}

.header-1__title {
  font-family: "Rubik", sans-serif;
  font-weight: 700;
  font-size: 16px;
  color: #ffffff;
  text-transform: uppercase;
  line-height: 1.88;
}

.header__nav {
  padding: 8px 0 7px;
  background: rgba(20, 217, 196, 0.9);
}

.header__nav-list {
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  line-height: 2;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

.header__link {
  color: #ffffff;
  text-decoration: none;
}

.header__link:hover {
  color: #1d0029;
}

.tandem-code__link {
  text-decoration: none;
  color: #ffffff;
}

.tandem-code__link:hover {
  color: #1d0029;
}/* CSS HEADER MEDIA *//* CSS MEDIA 1200px*//* HERO CSS */.hero {
  background-image: url(../images/tandem.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 421px;
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.hero__text {
  font-family: "Open Sans", sans-serif;
  font-size: 1.125rem;
  font-weight: 600;
  line-height: 1.39;
  padding: 0.9375rem;
  text-align: center;
  background-color: white;
  transform: translatey(50%);
}/* HERO MEDIA *//*TEAM SECTION CSS*/.team {
  text-align: center;
  position: relative;
}

.team__anchor-class {
  position: absolute;
  left: 0;
  top: -60px;
}

.team__title {
  font-family: "Rubik", sans-serif;
  font-size: 24px;
  font-weight: 700;
  color: #14d9c4;
  line-height: 1.88;
  text-transform: uppercase;
  margin-bottom: 15px;
  margin-top: 54px;
}

.team-desc__article {
  padding-top: 15px;
  padding-bottom: 30px;
  position: relative;
  z-index: 1;
}

.text {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  color: #54585a;
  margin-bottom: 30px;
  line-height: 24px;
}

.team__text:nth-child(3) {
  margin-bottom: 0;
}

.rombo {
  width: 30px;
  height: 30px;
  background-color: white;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%) rotate(45deg);
}/*TEAM SECTION MEDIA 768px*//*TEAM SECTION MEDIA 1200PX*//* CAPACITIES CSS */.capacities {
  text-align: center;
  background-color: rgba(184, 184, 185, 0.2);
  padding: 30px 0 45px 0;
}

.str__title,.weak__title {
  color: #099d8d;
  font-family: "Rubik", sans-serif;
  font-size: 22px;
  line-height: 2.05;
  font-weight: 600;
}

.capacities-str {
  margin-bottom: 30px;
}

.weak-text:last-child {
  margin-bottom: 0px;
}/* CAPACITIES MEDIA 768PX*//* CAPACITIES 1200px MEDIA*//* WHO CSS */.who {
  text-align: center;
  position: relative;
  padding: 30px 0 45px 0;
}

.who__anchor-class {
  position: absolute;
  left: 0;
  top: -80px;
}

.who__title {
  margin: 0 0 30px 0;
  line-height: 45px;
  text-transform: uppercase;
  font-size: 24px;
  font-family: "Rubik", sans-serif;
  font-weight: 700;
  color: #099d8d;
}

.who__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: "Open Sans", sans-serif;
}

.who__list-person {
  margin-bottom: 45px;
}

.who__list-person:last-child {
  margin-bottom: 0;
}

.who__list-image {
  width: 180px;
  height: 180px;
  margin: auto;
}

.who__image {
  width: 100%;
  border: solid 5px rgba(0, 0, 0, 0.08);
  border-radius: 4px;
  box-sizing: border-box;
}

.andre__image:hover {
  content: url("../images/andre-1x1.gif");
}

.coco__image:hover {
  content: url("../images/coco-1x1.gif");
}

.cris__image:hover {
  content: url("../images/cris-1x1.gif");
}

.eva__image:hover {
  content: url("../images/eva-1x1.gif");
}

.patri__image:hover {
  content: url("../images/patri-1x1.gif");
}

.who__name {
  margin: 15px 0 0 0;
  font-size: 20px;
  font-weight: normal;
  line-height: 2.25;
}

.who-text {
  margin: 0;
  color: #54585a;
  line-height: 1.5;
  font-size: 16px;
}

.social-list {
  margin: 15px auto 0 auto;
  padding: 0;
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 6px 0;
}

.social-list__icon:not(:last-child):after {
  content: "·";
  font-size: 16px;
  margin-right: 5px;
}

.social-list__link {
  text-decoration: none;
  color: #099d8d;
}

.social-list__link:hover {
  color: #1d0029;
}

.fab:hover,.fas:hover {
  transform: scale(1.5);
}/* MEDIA WHO SECTION 768px*//* WHO SECTION MEDIA 1200PX*/.footer {
  font-family: "Open Sans", sans-serif;
  border-top: 1px solid #b8b8b9;
  padding: 30px 0 45px 0;
  height: 250px;
  box-sizing: border-box;
}

.footer__text {
  text-align: center;
  font-size: 0.875rem;
  color: #b8b8b9;
  margin: 0 0 20px 0;
  line-height: 1.43;
}

.footer__nav {
  box-sizing: border-box;
  margin: 0 0 30px 0;
}

.footer__nav-list {
  list-style: none;
  padding: 0;
  margin: 0;
  line-height: 1.43;
  font-size: 0.875rem;
  font-weight: 500;
  color: #54585a;
  text-transform: uppercase;
  text-align: center;
  opacity: 0.75;
}

.footer__link {
  font-size: inherit;
  color: inherit;
  text-decoration: none;
  font-weight: 500;
}

.footer__link:hover {
  color: #1d0029;
}

.footer__logo-ada {
  height: 45px;
  margin: 0 auto;
  background-color: white;
  text-align: center;
}

.logo-ada__img {
  height: 100%;
}/* FOOTER MEDIA 768px*//* FOOTER MEDIA 1200px*/.main__herocontact {
  background-image: url("../images/grupo.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 300px;
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}/* CSS MEDIA HERO CONTACT 1200px*//* CSS DESCRIPTION*/.form {
  padding: 30px 0 60px 0;
  margin: 0;
}

.contact__title {
  text-align: center;
  font-family: "Rubik", sans-serif;
  font-size: 24px;
  font-weight: 700;
  color: #099d8d;
  text-transform: uppercase;
  line-height: 1.88;
  margin-bottom: 15px;
}

.contact__subt {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  color: #54585a;
  text-align: center;
  margin-bottom: 20px;
}

.contact__text {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  color: #54585a;
  text-align: center;
  line-height: 24px;
}/*CSS FORM*/.form-info {
  margin: 58px 0;
  font-size: 12px;
  white-space: pre;
 /*Para dejar espacio en blanco delante del asterisco*/
  font-family: "Open Sans", sans-serif;
  text-align: center;
  color: #54585a;
}

.contact-form {
  display: flex;
  flex-direction: column;
  align-content: center;
}

.contact-form-label {
  color: #54585a;
  font-size: 14px;
  margin-top: 13px;
  margin-bottom: 7px;
  text-align: left;
}

.contact-form-input {
  box-sizing: border-box;
  padding: 11px 15px 15px;
  width: 290px;
  height: 45px;
  border: solid 1px #b8b8b9;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.contact-form_message {
  box-sizing: border-box;
  width: 290px;
  height: 105px;
  border: solid 1px #b8b8b9;
  padding: 11px 15px 15px;
}

.contact-form_button {
  width: 290px;
  height: 45px;
  border-radius: 4px;
  background-color: #099d8d;
  color: white;
  margin-top: 30px;
  text-align: center;
}

.asterisco {
  color: #14d9c4;
}

.contact-form-input::placeholder {
  color: #b8b8b9;
}

.contact-form_message::placeholder {
  color: #b8b8b9;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
}/*A TOODOS LOS INPUT
-webkit-appearance: none;
-moz-appearance: none;
appearance: none;*/@media all and (min-width: 21.75rem) {
  .hero__text {
    transform: translatey(45%);
  }
}

@media all and (min-width: 48rem) {
  .container {
    margin: 0 2.8125rem;
  }

  .hero__text {
    transform: translatey(0%);
    background-color: rgba(255, 255, 255, 0.8);
    max-width: 60%;
  }
}

@media all and (min-width: 75rem) {
  .container {
    margin: 0 auto;
    width: 57.75rem;
  }

  .hero {
    height: 550px;
  }

  .hero__text {
    transform: translatey(0%);
    background-color: rgba(255, 255, 255, 0.8);
    max-width: 60%;
    font-size: 1.5rem;
    line-height: 1.25;
  }
}

@media all and (min-width: 768px) {
  .header {
    display: flex;
    justify-content: space-between;
  }

  .header-1 {
    flex-grow: 1;
    text-align: left;
  }

  .header__nav {
    width: 50%;
    padding: 15px 0;
    background-color: rgba(9, 157, 141, 0.95);
  }

  .header__nav .container {
    margin: 0 45px 0 0;
  }

  .header__link:hover {
    color: #1d0029;
  }

  .header__nav-list {
    justify-content: flex-end;
  }

  .header__nav-item {
    padding-right: 1.125rem;
  }

  .header__nav-item:last-child {
    padding-right: 0;
  }

  .team__anchor-class {
    top: -35px;
  }

  .team__title {
    color: #099d8d;
    margin-top: 0;
  }

  .team-desc__article {
    padding: 45px 0 60px;
  }

  .capacities {
    padding: 45px 0 60px 0;
  }

  .capacities .container {
    display: flex;
    flex-direction: row;
  }

  .capacities-str {
    width: 50%;
    box-sizing: content-box;
    margin: 0 15px 0 0;
  }

  .capacities-weak {
    width: 50%;
    box-sizing: content-box;
    margin-left: 15px;
  }

  .str-text:last-child,.weak-text:last-child {
    margin-bottom: 0px;
  }

  .grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 30px;
  }

  .grid-item1-odd {
    grid-column: 1/3;
  }

  .grid-item2-odd {
    grid-column: 3/7;
  }

  .grid-item1-even {
    grid-column: 5/7;
  }

  .grid-item2-even {
    grid-column: 1/5;
  }

  .who {
    padding: 45px 0 60px 0;
  }

  .who__title {
    margin-bottom: 40px;
  }

  .who__name {
    margin-top: 0;
  }

  .who__anchor-class {
    top: -60px;
  }

  .who__list-person {
    margin-bottom: 65px;
    display: flex;
    text-align: left;
  }

  .who__list-person:nth-child(2n) {
    text-align: right;
    justify-content: flex-end;
  }

  .who__list-person:nth-child(2n) .who__list-image {
    order: 1;
  }

  .social-list {
    justify-content: flex-start;
  }

  .who__list-person:nth-child(2n) .social-list {
    justify-content: flex-end;
  }

  .who__list-image {
    margin: 0;
    width: 205px;
    min-width: 205px;
    height: 205px;
  }

  .who__list-person:nth-child(2n) .who__list-image {
    margin-right: 0;
  }

  .footer {
    padding: 30px 0 60px 0;
    height: 170px;
    box-sizing: border-box;
  }

  .wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 45px 45px;
  }

  .item1 {
    grid-column: 1/2;
  }

  .item2 {
    grid-column: 2/3;
    grid-row: 1/3;
  }

  .item3 {
    grid-column: 1/2;
  }

  .footer__text {
    text-align: left;
  }

  .footer__nav {
    margin: 0;
  }

  .footer__nav-list,.footer__link {
    line-height: 2.14;
    text-align: right;
  }

  .footer__logo-ada {
    text-align: left;
    margin: 0;
  }

  .form {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .form-info {
    text-align: center;
  }

  .contact-form-label {
    width: 442px;
    text-align: left;
  }

  .contact-form-input {
    width: 442px;
    height: 45px;
  }

  .contact-form_message {
    width: 442px;
    height: 105px;
  }

  .contact-form_button {
    width: 206px;
    height: 45px;
    align-self: flex-end;
  }
}

@media all and (min-width: 1200px) {
  .header {
    display: flex;
    justify-content: space-between;
  }

  .header-1 {
    flex-grow: 1;
    text-align: left;
  }

  .header__nav {
    width: 50%;
    padding: 15px 0;
    background-color: rgba(9, 157, 141, 0.95);
  }

  .header__nav .container {
    margin: 0 45px 0 0;
  }

  .header-1 .container {
    margin: 0 0 0 auto;
    width: 462px;
  }

  .header__nav .container {
    margin: 0 auto 0 0;
    width: 462px;
  }

  .header__link:hover {
    color: #1d0029;
  }

  .header__nav-list {
    justify-content: flex-end;
  }

  .header__nav-item {
    padding-right: 1.125rem;
  }

  .header__nav-item:last-child {
    padding-right: 0;
  }

  .team-desc__article {
    width: 600px;
    margin: 0 auto;
  }

  .grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 45px;
  }

  .grid-item1-odd {
    grid-column: 1/3;
  }

  .grid-item2-odd {
    grid-column: 3/6;
  }

  .grid-item1-even {
    grid-column: 5/7;
  }

  .grid-item2-even {
    grid-column: 2/5;
  }

  .who-text {
    max-width: 440px;
  }

  .who__list-image {
    width: 278px;
    min-width: 278px;
    height: 278px;
  }

  .who__list-person {
    margin-bottom: 60px;
  }

  .footer {
    padding: 30px 0 45px 0;
    height: 160px;
    box-sizing: border-box;
  }

  .main__herocontact {
    height: 400px;
  }

  .form {
    display: flex;
    justify-content: center;
  }

  .contact-form {
    width: 601px;
  }

  .contact-form-input {
    width: 395px;
  }
}
